@import '@css/index.scss';

.burgerMenuDescrTitle {
  @include mobile {
    font-size: 27px;
  }

  @include font(35px, 300, 0.05em);
  padding: 12px 0 24px 0;
  text-align: center;
}

.burgerMenuDescrItem {
  @include mobile {
    // max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 0 5px;
  }

  display: flex;
  justify-content: space-between;
  /* max-width: 550px; */
  padding: 0 10px;
  margin: 0 auto;
  margin-bottom: 32px;

  &:last-of-type {
    @include mobile {
      margin-bottom: 32px;
    }

    margin-bottom: 52px;
  }
}

.burgerMenuDescrText {
  @include font(18px, 300, 0.01em);
  max-width: 281px;

  @include mobile {
    font-size: 14px;
    max-width: 180px;
  }
}

.burgerMenuDescrPrice {
  @include mobile {
    font-size: 14px;
  }

  @include font(18px, 300, 0.01em);
  /* margin-left: 20px; */
}

/* .burgerMenuDescrItemCenter{
    display: flex;
    justify-content: center;
} */

.burgerMenuDescrTextItalic {
  @include mobile {
    font-size: 12px;
  }

  @include font(16px, 400, 0.01em);

  margin: 0 auto;
  font-style: italic;
}

.burgerMenuSpetificList {
  display: block;
  margin-left: 32px;
}

.butgerMenuSubtitle {
  @include mobile {
    font-size: 18px;
  }

  @include font(25px, 300, 0.05em);
  margin-bottom: 12px;
}

.burgerMenuItemSpetific {
  @include mobile {
    font-size: 14px;
  }

  @include font(18px, 300, 0.01em);
  margin-left: 16px;
  margin-bottom: 8px;

  &:last-child {
    margin-bottom: 20px;
  }
}
